$backgroundColor: #ffffff;
$bodyColor: #000000;
$bodyFont: "National2-Regular",-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";

* {
  -o-font-smoothing: antialiased;
	-moz-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
  line-height: 1.7em;
}

@font-face {
  font-family: 'National2-Regular';
  src: url('../fonts/National2-Regular.eot');
  src: url('../fonts/National2-Regular.eot?#iefix') format('embedded-opentype'),
       url('../fonts/National2-Regular.ttf') format('truetype'),
       url('../fonts/National2-Regular.woff') format('woff'),
       url('../fonts/National2-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'National2-Medium';
  src: url('../fonts/National2-Medium.eot');
  src: url('../fonts/National2-Medium.eot?#iefix') format('embedded-opentype'),
       url('../fonts/National2-Medium.ttf') format('truetype'),
       url('../fonts/National2-Medium.woff') format('woff'),
       url('../fonts/National2-Medium.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

html {
  font-size: 16px;
}

@media screen and (min-width: 320px) {
  html {
    font-size: calc(16px + 6 * ((100vw - 320px) / 680));
  }
}

@media screen and (min-width: 992px) {
  html {
    font-size: 22px;
  }
}

figcaption {
  font-size: 15px;
}

.container-fluid {
	padding-left: 30px;
	padding-right: 30px;
}

a {
	color: #00cc76;
	text-decoration: underline;
}

a:hover, a:visited, a:focus {
	color: #00cc76;
	text-decoration: none;
}











